@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.cv-form {
  width: 37.1em;
  height: 52.6em;

  grid-area: cv-form;
  justify-self: center;

  display: grid;
  grid-template-areas: "name name"
                       "contact profile"
                       "skills work"
                       "education work";

  grid-template-rows: auto auto auto 1fr;
  grid-template-columns: 1fr 2fr;
}

@media (max-width: 600px) {
  .cv-form {
    width: 100%;
    height: auto;
    grid-template-areas: "name"
                          "contact"
                          "profile"
                          "skills"
                          "work"
                          "education";
    grid-template-rows: repeat(6, auto);
    grid-template-columns: 1fr;
  }
}

.cv-section {
  padding: 2em;
  position: relative;
  display: flex;
  flex-direction: column;
}

.cv-section ul {
  list-style-type: none;
}

.cv-section input, .cv-section textarea {
  margin-bottom: 1em;
  padding: 5px;
  border: none;
  border-radius: 1em;
  line-height: 2em;
  width: 100%;
}

.section-title {
  margin-bottom: 1.5em;
  font-size: 1.3em;
  font-weight: 400;
  text-transform: uppercase;
}


.btn-container {
  text-align: center;
  cursor: pointer;
}

.edit-btn.btn-container, .cancel-btn.btn-container {
  position: absolute;
  top: 1em;
  right: 0;
  width: 3em;
  background-color: rgba(255,255,255,0.2);
  color: rgba(0,0,0,0.4);
}

.edit-btn.btn-container:hover, .cancel-btn.btn-container:hover {
  color: rgba(0,0,0,0.7);
}

.save-btn.btn-container, .add-btn.btn-container, .delete-btn.btn-container {
  margin-top: 0.5em;
  border-radius: 5px;
}

.save-btn.btn-container {
  color: white;
  border: 2px solid rgba(105,126,202,1);
  background-color: rgba(105,126,202,1);
}

.save-btn.btn-container:hover {
  border: 2px solid rgba(105,126,202,0.8);
  background-color: rgba(105,126,202,0.8);
}

.add-btn.btn-container {
  color: rgba(104,101,158,1);
  border: 2px solid rgba(104,101,158,1);
}

.add-btn.btn-container:hover {
  color: white;
  border: 2px solid rgba(104,101,158,0.8);
  background-color: rgba(104,101,158,0.8);
}

.delete-btn.btn-container {
  width: 8em;
  padding: 0.3em;
  color: rgba(226,104,104, 1);
  text-transform: capitalize;
  border: 2px solid rgba(226,104,104, 1);
}

.delete-btn.btn-container:hover {
  color: white;
  border: 2px solid rgba(226,104,104, 0.8);
  background-color: rgba(226,104,104, 0.8);
}

.name-section {
  position: relative;
  height: 4em;
  background-color: #DADADA;
  grid-area: name;
}

.name-section h1 {
  position: absolute;
  left: 0;
  padding: 0.5em 0 0.5em 0.5em;
  min-width: 60%;
  max-width: 80%;
  background-color: #F3F5F8;
  text-transform: uppercase;
}

.contact-info-section {
  background-color: #F2F2F2;
  grid-area: contact;
}

.contact {
  display: flex;
  align-items: center;
}

.contact-icon {
  padding-right: 0.5em;
}

.profile-section {
  background-color: #C7D3DC;
  grid-area: profile;
}

.work-experience-section {
  background-color: #CEC3EE;
  grid-area: work;
}

.job-entry {
  margin-bottom: 2em;
}

.job-entry tr {
  margin: 0.5em 0;
}

.job-title {
  text-transform: uppercase;
}

.job-company {
  font-style: italic;
  flex: 1 1;
  margin-right: 1em;
}

.company-years-row {
  display: flex;
  justify-content: space-between;
}

.job-years {
  max-width: 8em;
  display: inline-block;
}

ul.job-description {
  margin-left: 1em;
  margin-bottom: 1em;
}

.job-description-entry {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.list-bullet {
  padding-right: 1em;
}

.job-description .add-btn.btn-container {
  margin-left: 1em;
}

.skills-section {
  background-color: #E5C9F2;
  grid-area: skills;
}

.skill {
  margin-bottom: 0.5em;
}

.skill-name {
  padding-bottom: 0.5em;
}

/* create full bar */
.skill-rating-container {
  width: 100%;
  background-color: #C4C4C4;
}

/* create bar with length corresponding to rating rating  */
.skill-rating {
  height: 1em;
  background-color: #003169;
}

.skill-rating-1 {
  width: 20%;
}

.skill-rating-2 {
  width: 40%;
}

.skill-rating-3 {
  width: 60%;
}

.skill-rating-4 {
  width: 80%;
}

.skill-rating-5 {
  width: 100%;
}

.education-section {
  background-color: #F2F2F2;
  grid-area: education;
}

.school-entry {
  margin-bottom: 2em 0;
}

.school-entry > * {
  margin-bottom: 0.5em;
}

.school-qualification {
  font-style: italic;
}

* {
    margin: 0;
    padding: 0;
  }

a:link, a:active, a:visited, a:hover {
  text-decoration: none;
}

.app-wrapper {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-areas: "sidebar cv-form";
  font-family: 'Roboto', sans-serif;
  color: rgba(0,0,0,0.8);
}

@media (max-width: 600px) {
  .app-wrapper {
    grid-template-columns: 1fr;
    grid-template-areas: "cv-form"
                          "sidebar";
  }
}

@media (min-width: 1400px) {
  .app-wrapper {
    font-size: 2em;
  }

  input, textarea {
    font-size: 0.8em;
  }
}

.sidebar {
  grid-area: sidebar;
  min-width: 200px;
  text-align: center;
  background-color: thistle;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.sidebar > * {
  padding: 1em;
}

.credits-wrapper {
  background-color: rgba(0,0,0,0.8);
  cursor: pointer;
}

.credits-wrapper:hover {
  background-color: rgba(0,0,0,0.6);
}

.credits-wrapper a {
  color: #F2F2F2;
}

.github-mark {
  width: 2em;
  height: 2em;
  -webkit-filter: invert(1);
          filter: invert(1);
  margin-bottom: 0.5em;
}


